          <v-col>
            <v-img
              src="https://res.cloudinary.com/dkod5pdlc/image/upload/f_auto,q_auto/v1639498861/terapiacl/servicios/tratamientos-medicos/tratamientos-medicos-1"
            />
          </v-col>
        </v-row>
        <splitter />
        <v-row class="tratamientos-medicos_row__wrap1">
          <v-col class="wrap1_col">
            <div class="wrap1_main">
              <p>
                La Terapia o <b>Tratamiento Médico</b> es un conjunto de medios cuya
                finalidad es la curación o el alivio de enfermedades o síntomas.
                En Psiquiatría y Neurología, la <strong>psicofarmacoterapia</strong> o sea el
                tratamiento con psicofármacos es el medio más usado para tratar
                los trastornos psiquiátricos y neurológicos.
              </p>
              <p>
                La farmacología se lleva a cabo a través de la prescripción de
                fármacos mediante la <strong>receta médica</strong> la que puede ser o no
                retenida en la farmacia. La receta médica es un documento legal,
                por medio del cual los médicos legalmente capacitados prescriben
                la medicación al paciente. La prescripción del fármaco es un
                proceso individual de un médico autorizado a un paciente
                consultante.
              </p>
              <p>
                Todo tratamiento médico requiere de una terapia psicológica
                complementaria. Ambos aumentan la efectividad del tratamiento.
              </p>
              <p>
                La visita a un médico no siempre implica la prescripción de un
                medicamento, ya que el profesional puede evaluar que no es
                necesario y lo que realmente necesita el consultante es un
                tratamiento psicológico.
              </p>
            </div>
          </v-col>
        </v-row>
        <splitter />
        <v-row class="tratamientos-medicos_row__img2">
          <v-col>
            <v-img
              src="https://res.cloudinary.com/dkod5pdlc/image/upload/f_auto,q_auto/v1639498861/terapiacl/servicios/tratamientos-medicos/tratamientos-medicos-2"
            />
          </v-col>
        </v-row>
        <splitter />
        <v-row class="tratamientos-medicos_row__wrap2">
          <v-col>
            <p>
              Para la prescripción de un medicamento el médico tiene en
              consideración en primer lugar el diagnóstico. Considera que el
              fármaco y la dosificación sean los apropiados. Considera la edad.
              Si es mujer, considera si está embarazada o en periodo de
              lactancia. Toma en cuenta la interacción con otros fármacos.
            </p>
            <p>
              El paciente debe tener controles periódicos con su médico para
              revisar los efectos de los fármacos, su uso correcto, los efectos
              secundarios que muchos medicamentos producen. Además los controles
              son necesarios para ajustar la dosificación, sea para aumentarla o
              disminuirla o para cambiar los fármacos por otros, hasta dar con
              aquellos y su dosificación que beneficien al paciente.
